html, body { height: 100% }

body {
    overflow-x: hidden;
    overflow-y: auto;
    // min-height: 100vh;
    background: #f3f3f3;
    color: #3d3e40;
    font-family: "Lato", sans-serif;
}

a {
    @include transition(all ease 300ms);

    &:hover,
    &:focus {
        outline: none;
        @include box-shadow(none);
    }
}

img {
    @extend .img-fluid;
}

.container-fluid {
    @extend .container;
}

.splash {
    background-image: url("../img/splashbg.png");
    background-color: $secondary;
    background-repeat: repeat;
    background-size: auto 90px;
    background-position: top;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 9999;

    &.loaded {
        display: none;
    }

    img {
        position: absolute;
        top: 50%;
        @include transform(translateY(-50%));
        left: 0;
        right: 0;
        margin: 0 auto;
        text-align: center;
        height: 100px;
    }
}

main {
    height: 100%;
}

.container {
    @include media-breakpoint-down(md) {
        max-width: 100%;
    }
}

section {
    &.block {
        padding: 20px 0;
        position: relative;
    }

    h4 {
        color: $primary;
        font-size: 22px;
        font-weight: bold;
        margin-bottom: 15px;
    }
}
.btn {
    font-size: 15px;
    padding: 10px 10px;
    text-transform: uppercase;
    font-weight: 600;
    @include border-radius(6px);
}

.btns{
    width: 100%;
    @include media-breakpoint-up(md){
        text-align: center;
    }
    
    .btn{
        width: 100%;
        display: block;
        margin-bottom: 5px;

        @include media-breakpoint-up(md){
            width: auto;
            display: inline-block;
        }
    }
}

.form-group{
    .form-control{
        @include border-radius(6px);
    }
}

ul {
    list-style: none;
    padding: 0;
    margin-bottom: 15px;

    li {
        padding-left: 30px;
        position: relative;

        &:before {
            position: absolute;
            left: 0;
            content: "\f00c";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
        }
    }
}

.cover{
    background-image: url("../img/splashbg.png");
    background-color: $secondary;

    background-repeat: repeat;
    background-size: auto 100px;
    background-position: center;
    padding: 40px 0;
    text-align: center;
    display: flex;
    flex: 0 0 50%;
    align-items: center;
    @include box-shadow(0 6px 0px rgba(0,0,0,0.13) );
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;

    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: linear-gradient(180deg, rgba(0,161,225,1) 0%, rgba(0,212,255,0.35057773109243695) 100%)
    }

    img{
        position: relative;
        z-index: 3;
        width: 40%;
        max-width: 140px;
    }

    @include media-breakpoint-down(sm){
        flex: 0 0 180px;
    }
}
