.login{
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-between;
    min-height: 100vh;
    min-height: -webkit-fill-available;

    @include media-breakpoint-up(md){
        flex-flow: row;
        height: 100vh;
    }

    .cover{

    }

    .loginBody{
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        padding: 30px 20px 20px;
        flex-grow: 1;
        max-width: 500px;
    }

    .top{
        flex-grow: 1;
        height: 100%;
        width: 100%;
    }

    .bottom{
        position: relative;
        width: 100%;
    }

    .form-group{
        label{
            padding: 0 10px;
            font-size: 14px;
            font-weight: 600;
            color: #000;

            &.custom-control-label{
                padding: 0;
            }
        }

        p{
            font-weight: 600;
            color: #000;
        }

        small{
            display: block;
            font-weight: 400;
            padding: 20px 0 5px;
            text-align: center;
            color: #4f4d4d;
            font-size: 12px;

            a{
                text-decoration: underline;
            }
        }

        i{
            position: absolute;
            z-index: 99;
            color: #000;
            left: 10px;
            top: 19px;
            @include transform(rotate(100deg));
        }

        .form-control{
            border: none;
            background: #fff;
            color: #000;
            padding: 12px 15px;
            font-weight: 600;
            font-size: 14px;
            height: calc(1.5em + .75rem + 12px);
            margin: 0;
            margin-top: 5px;

            &.form-cc{
                flex: 0 0 80px;
                border-right: 1px solid #f1f1f1;
                padding: 10px 0px 10px 27px;
                @include border-radius(6px 0 0 6px);
            }
        }

        .input-group{
            justify-content: center;
        }

        .input-otp{
            .form-control{
                @include border-radius(4px);
                text-align: center;
                padding: 12px 8px;
                max-width: 70px;

                &:not(:last-child){
                    margin-right: 5px;
                }
            }
        }
    }
}