.modal{
    text-align: center;

    .modal-header{
        position: relative;
        background: transparent;
        border: none;
        padding: 0;

        &:after, &:before{
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            top: -55px;
            margin: 0 auto;
            z-index: 1;
            height: 60px;
            width: 80px;
            @include border-radius(0 0 60px 60px);
        }

        &:before{
            z-index: 4;
            height: 40px;
            @include border-radius(0 0 40px 40px);
        }

        &:after{
            background: #f3f3f3;
            z-index: 3;
            width: 100px;
            height: 50px;
        }

        i{
            position: absolute;
            z-index: 5;
            top: -85px;
            left: 0;
            right: 0;
            font-size: 30px;
            margin: 0 auto;
            height: 60px;
            width: 60px;
            line-height: 60px;
            background: $secondary;
            color: #fff;
            @include border-radius(50%);
        }
    }

    .modal-content{
        position: relative;
        padding-top: 55px;
        @include border-radius(8px);

        &:after{
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            bottom: -12px;
            width: 80%;
            height: 12px;
            background: $primary;
            margin: 0 auto;
            @include border-radius(0 0 7px 7px);
        }

        .modal-body{
            
        }

        h4{
            font-size: 18px;
            margin-bottom: 20px;
        }

        p{
            font-size: 15px;
            color: #000;
        }

        .btns{

            .btn{
                font-size: 14px;
                padding: 8px 10px;
                @include media-breakpoint-down(sm) {
                    display: block;
                    margin-bottom: 5px;
                }
                
            }
        }
    }

    &.modalDanger{
        .modal-header{
            &:before{
                background: lighten($danger, 30%);
            }
            i{
                background: $danger;
            }
        }

        .modal-content{
            &:after{
                background: $danger;
            }

            h4{
                color: $danger;
            }
        }
    }

    &.modalSuccess{
        .modal-header{
            &:before{
                background: lighten($success, 30%);
            }
            i{
                background: $success;
            }
        }

        .modal-content{
            &:after{
                background: $success;
            }

            h4{
                color: $primary;
            }
        }
    }
}